import {BUSINESS_DIMENSION,LABEL} from "./constants";
type BusinessCategory = "BUSINESS_DIMENSION" | "BUSINESS_CONTENT" | null;
const ALL_NODES = [];
/**
 * Calles the Container node constructor if it has a proper business category
 * @param {*} subContainer 
 * @param {*} depth 
 */
function createContainerNode(subContainer, depth = 0) {
    switch(subContainer.businessCategory){
        case "BUSINESS_DIMENSION":return new BizDimensionNode(subContainer, depth);
        case "BUSINESS_CONTENT": return new BizContentNode(subContainer,depth);
        default: return new ContainerNode(subContainer,depth);
    }
}


export class ContainerNode {
    businessCategory: BusinessCategory;
    id: string;
    depth: number;
    label: string;
    nodes: {
        [string]: ContainerNode
    }
    type = "ContainerNode";
    constructor({ businessCategory, id, label, qualifiedId, searchBoost, container }, depth = 0) {
        _.assign(this, { businessCategory, id, label, qualifiedId, searchBoost, depth });
        if (!container || container.length === 0) return;
        this.nodes = {};
        const newDepth = depth++;
        function addDepth(subContainer) {
            return createContainerNode(subContainer, newDepth);
        }
        container.map(addDepth).filter(node => node !== null).forEach(node => {
            if (this.nodes[node.label]) {
                console.warn(`ContainerNode::The node with label ${label} already exists, skipping`);
                return;
            };
            this.nodes[node.label] = node;
            ALL_NODES.push(node);
        });
    }
    /**
     * Not necessarily the URI, but the information required to find the URI
     */
    asUri(){
        return {containerQId:this.id};
    }
};


export class BizContentNode extends ContainerNode {
    type="BizContentNode";
};

export class BizDimensionNode extends ContainerNode {
    type="BizDimensionNode";
    entNodes:BizDimensionEntity[]=[];
    createBizContentIndex(bizContentNode){
        if(bizContentNode.label in this.nodes){
            return this.entNodes.map(node => node.createBizContentDatum(bizContentNode));
        }else{
            return [];
        }
    }
}

export class BizDimensionEntity {
    label: string;
    identity: string;
    type = "BizDimensionEntity";
    hasAddedLazyEntries = false;
    bizDimensionNode: BizDimensionNode;
    constructor(payload) {
        _.assign(this,payload);
        this.bizDimensionNode.entNodes.push(this);
    };
    createIndex() {
        if (this.hasAddedLazyEntries || !this.bizDimensionNode || !this.bizDimensionNode.nodes) return [];
        const bizDimEnt = this;
        const index = _.values(this.bizDimensionNode.nodes).map(bizContentNode => new BizDimEntSubNode({ bizDimEnt, bizContentNode }));
        this.hasAddedLazyEntries = true;
        return index;
    }
    createBizContentDatum(superBizContentNode){
        if(superBizContentNode.label in this.bizDimensionNode.nodes){
            const bizDimEnt = this;
            const bizContentNode = this.bizDimensionNode.nodes[superBizContentNode.label];
            return new BizDimEntSubNode({ bizDimEnt, bizContentNode });
        }else{
            return null;
        }
    }
    /**
     * Not necessarily the URI, but the information required to find the URI
     */
    asUri(){
        const {containerQId} = this.bizDimensionNode.asUri();
        const {identity} = this;
        return {containerQId,identity};
    }
};

export class BizDimEntSubNode {
    label: string;
    bizDimEnt: BizDimensionEntity;
    bizContentNode: ContainerNode;
    type = "BizDimEntSubNode";
    constructor(payload) {
        _.assign(this,payload);
        //TODO: i18n
        this.label = `${this.bizContentNode.label} for ${this.bizDimEnt.label}`;
    }
    /**
     * Not necessarily the URI, but the information required to find the URI
     */
    asUri(){
        const {containerQId,identity} = this.bizDimEnt.asUri();
        const content = this.bizContentNode.asUri();
        return {containerQId,subContainerQId:content.containerQId,parentIdentity:identity};
    }
}


/**
 * First level nodes require a business category
 * @param {*} container 
 */
function createBizCategoryNodes(container) {
    if (!container.businessCategory) return null;
    return createContainerNode(container);
}

export class ContainerGraph {
    bizDimensionNodes:{
        [string]:BizDimensionNode
    }
    rootNodes:{
        [string]:ContainerNode
    }
    ALL_NODES:ContainerNode[]
    constructor(ebTemplate) {
        const { container } = ebTemplate.dataDefinition;
        this.rootNodes = {};
        container.map(createBizCategoryNodes).filter(node => node !== null).forEach(node => {
            if (this.rootNodes[node.label]) {
                console.warn(`ContainerGraph::The node with label ${node.label} already exists, skipping`);
                return;
            };
            this.rootNodes[node.label] = node;
            ALL_NODES.push(node);
        });
        this.ALL_NODES = ALL_NODES;
        this.bizDimensionNodes = {};
        ALL_NODES.filter(node => node.businessCategory === BUSINESS_DIMENSION).forEach(node => {
            if (this.bizDimensionNodes[node.id]) {
                console.warn(`ContainerGraph::A business dimension node with label ${node.label} already exists, skipping`);
                return;
            }
            this.bizDimensionNodes[node.id] = node;
        });
    }
    createBizDimEntity({ label, businessDimensionType, identity }) {
        const bizDimensionNode = this.bizDimensionNodes[businessDimensionType];
        return new BizDimensionEntity({ label, bizDimensionNode, identity });
    }
    //Only two deep
    createIndex() {
        return _.values(this.rootNodes);
    }
    createBizContentIndex(bizContentNode:BizContentNode){
        if(bizContentNode instanceof BizContentNode){
            return _(this.bizDimensionNodes).values().map(node => node.createBizContentIndex(bizContentNode)).flatten().value();
        }else{
            throw new Error("#createBizContentIndex requires a BizContentNode");
        }
    }
}


