require('es6-promise/auto');
import {BUSINESS_DIMENSION,LABEL,SELECT_ENTER_DISP,RESULTS_LIMIT,GROW_LENGTH,TYPEAHEAD_OPTIONS,
SHRUNK,GROWING,SHRINKING,asyncNoop
} from "./src/constants";
import {ContainerGraph,ContainerNode,BizDimensionEntity,BizDimEntSubNode} from "./src/graph";
import Indexer from "./src/Indexer";
require("./src/style.css");

const entityMap = {
  '&': '&amp;',
  '<': '&lt;',
  '>': '&gt;',
  '"': '&quot;',
  "'": '&#39;',
  '/': '&#x2F;',
  '`': '&#x60;',
  '=': '&#x3D;'
};

function escapeHtml(string) {
  return String(string).replace(/[&<>"'`=\/]/g, s => entityMap[s]);
}


//TODO: XSS escaping!!!

function suggestionTemplate(node) {
    const { label, id } = node;
    let innerContent = label;
    if (node instanceof BizDimensionEntity) {
        const { bizDimensionNode } = node;
        const dimension = bizDimensionNode.label;
        innerContent = `<span>${label}</span><span class="pull-right eb-tt-biz-dimension">${dimension}</span>`;
    } else if (node instanceof BizDimEntSubNode) {
        const { bizDimEnt } = node;
        const { bizDimensionNode } = bizDimEnt;
        const dimension = bizDimensionNode.label;
        innerContent = `<span>${label}</span><span class="pull-right eb-tt-biz-dimension">${dimension}</span>`;
    }
    return `
        <div data-id="${id}" class="container-fluid">
            ${innerContent}
        </div>
    `;
}

/**
 * A template that will say "Search for ${query}" when no results are returned 
 * TODO: XSS escaping
 */
function notFoundTemplate({query}){
    return `
    <div class="tt-selectable container-fluid eb-search-for">
        Search for <strong>"${escapeHtml(query)}"</strong>
    </div>
    `;
}

const templates = {
    suggestion:suggestionTemplate,
    notFound:notFoundTemplate
}

export class SearchComponent {
    lastSelected = "";
    enteredQuery = "";
    selectedTs = new Date();
    constructor({ mountPoint, asyncData }) {
        this.indexer = new Indexer(asyncData);
        this.$e = $(mountPoint);
        this.$e.typeahead(TYPEAHEAD_OPTIONS,
            {
                name: 'entities',
                source: this.indexer.hound,
                limit: RESULTS_LIMIT,
                display: LABEL,
                templates
            }
        ).bind("typeahead:select", this.updateValue)
            //.bind("typeahead:change",this.onChange)
            //.keydown(this.onKeyDown)
            .keyup(this.updateValue);
        this.bouncedShrinkOrExpand = _.debounce(this.indexer.shrinkOrExpand,100);
    }
    /**
     * This fires when an enter is enter independent of a select
     */
    onTrueEnter = (payload) => {
        console.warn("onTrueEnter not set", payload);
    }
    onEnter = (e) => {
        e.preventDefault();
        let { value } = e.target;
        value = value.trim();
        this.enteredQuery = value;
        const enterTs = new Date();
        const timeDiff = enterTs - this.selectedTs;
        if (SELECT_ENTER_DISP < timeDiff) {
            console.debug("onTrueEnter time difference", timeDiff);
            const query = this.enteredQuery;
            const node = this.indexer.getOne(query);
            this.onTrueEnter({ query, node });
        }
        return false;
    }
    updateValue = (e) => {
        const keyCode = e.keyCode || e.which;
        if (keyCode === 13) {
            return this.onEnter(e);
        }
        let { value } = e.target;
        value = value.trim();
        this.selectedTs = new Date();
        this.lastSelected = value;
        const node = this.indexer.getOne(value);
        if (!node){
            this.bouncedShrinkOrExpand(value);
            return;
        };
        if (this.indexer.isGrowableNode(node)) {
            //Add lazy entries now
            this.indexer.growFromNode(node);
        }
    }

};













