type ExpansionState = "SHRUNK" | "GROWING" | "SHRINKING";
import {ContainerGraph,ContainerNode,BizDimensionEntity,BizDimEntSubNode,BizContentNode} from "./graph";
import {BUSINESS_DIMENSION,LABEL,SELECT_ENTER_DISP,RESULTS_LIMIT,GROW_LENGTH,TYPEAHEAD_OPTIONS,
SHRUNK,GROWING,SHRINKING,asyncNoop
} from "./constants";

function processBizDimEnts(value, businessDimensionType) {
    const index = [];
    value.forEach(entity => {
        let { name, shortName, identity } = entity;
        if (!name) return;
        name = name.trim();
        shortName = String(shortName || name).trim();
        if (name) index.push({label: name, businessDimensionType, identity });
        if (shortName !== name) index.push({ label: shortName, businessDimensionType, identity });
    });
    return index;
};

export default class Indexer {
    
    expansionState:ExpansionState=SHRUNK;
    containerGraph: ContainerGraph;

    constructor(asyncData){
        this.ids = [];
        this.hound = new Bloodhound({
            datumTokenizer: Bloodhound.tokenizers.obj.whitespace(LABEL),
            queryTokenizer: Bloodhound.tokenizers.whitespace,
            local: [],
            identify(obj) {return obj.label;}
        });
        this.asyncTemplate = asyncData.template;
        this.asyncData = asyncData;
        //Kick off async processing
        this.asyncReady = this._processAsyncData();
    }
    isGrowableNode(node){
        return node instanceof BizDimensionEntity || node instanceof BizContentNode;
    }
    growFromNode(node){
        if(node instanceof BizDimensionEntity){
            this.add(node.createIndex());
        }else if(node instanceof BizContentNode){
            this.add(this.containerGraph.createBizContentIndex(node));
        }
    }
    add(items){
        const newItems = items.filter(item => !~this.ids.indexOf(item[LABEL]));
        this.hound.add(newItems);
        this.ids = this.ids.concat(newItems.map(item => item[LABEL]));
    }
    clear(){
        this.ids = [];
        this.hound.clear();
    }
    _updateBizEntity = (content, key) => {
        const idx = processBizDimEnts(content, key).map(this._createBizDimEntity);
        this.add(idx);
    }
    _createBizDimEntity = (payload) => {
        return this.containerGraph.createBizDimEntity(payload);
    }
    _updateTemplate(ebTemplate) {
        this.containerGraph = this.containerGraph || new ContainerGraph(ebTemplate);
        const idx = this.containerGraph.createIndex();
        this.add(idx);
    }
    _processAsyncData = () => {
        return this.asyncTemplate.then(ebTemplate => {
            this._updateTemplate(ebTemplate);
            return _(asyncData).omit(["template"]).entries().map(([key, promise]) => {
                return promise.then(content => this._updateBizEntity(content, key));
            }).value();
        });
    }
    search = (query) => {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                this.hound.search(query, (data) => {
                    resolve(data);
                });
            });
        });        
    }
    getOne(query){
        return this.hound.get([query])[0];
    }
    /**
     * Called when the number of characters is less than the GROWTH_THRESHOLD
    */
    restart(){
        //avoid multiple calls
        if(this.expansionState === SHRINKING) return asyncNoop;
        this.expansionState = SHRINKING;
        return this.asyncReady.then(() => {
            this.clear();
            return this.hound
            .initialize(true)
            .then(this._processAsyncData)
            .then(() => {
                this.expansionState = SHRUNK;
            });
        });
    }
    shrinkOrExpand = (query:string) => {
        //This is because the component will not rerender until the next character input
        if(query.length < GROW_LENGTH){
            if(this.expansionState !== SHRUNK){
                return this.restart();
            }
            return asyncNoop;
        }else{
            return this.search(query).then((data) => {
                if(data.length < RESULTS_LIMIT){
                    let node;
                    for(let i=0;i < data.length;++i){
                        node = data[i];
                        if(this.isGrowableNode(node)){
                            break;
                        }
                    }
                    if(node){
                        this.growFromNode(node);
                    }
                    this.expansionState = GROWING;
                }
                return null;
            });
        }
    }
}


